"use strict";

var time = null;

module.exports = {
    verbose: function verbose(msg) {
        if (this.writeLog) {
            console.log("[" + new Date() + "] " + this.calculateTimeDiff() + msg);
        }
    },
    calculateTimeDiff: function calculateTimeDiff() {
        if (time) {
            var timeNow = new Date().valueOf();
            var ret = timeNow - time;
            time = timeNow;

            return "+" + ret + "ms ";
        } else {
            time = new Date().valueOf();

            return "";
        }
    }
};